@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: Lato;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: lightsteelblue; */
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navigationBtns {
  display: flex;
  flex-direction: row;
  /* justify-content: ; */
  margin: 2rem 0 4rem;
}

.prevBtn, .nextBtn {
  background-color: #e17b77;
  border: 2px solid #e17b77;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  font-weight: 100;
  padding: 10px 14px;
  letter-spacing: .5px;
  text-transform: uppercase!important;
}


.prevBtn {
  margin-right: 70px;
}

.nextBtn {
  margin-left: 70px;
}

.prevBtn:hover {
  background-color: white;
  color:#e17b77;
}

.nextBtn:hover {
  background-color: white;
  color:#e17b77;
}

button:hover {
  background-color: white;
  color:green;
}

.App {
  text-align: center;
  /* margin-bottom: 0px; */
}

.header {
    background-color: #013D5B;
    min-height: 10vh;
    display: flex; 
    /* flex-direction: column; */
    align-items: center;
    justify-content: center;
    font-size: 36px;
    color: white;
}

.material-icons {
    margin-right: 10px;
    margin-left: 10px;
}
.footer {
    background-color: #013D5B;
    min-height: 10vh;
    display: flex; 
    flex-direction: column; 
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    position: fixed;
    bottom: 0;
    left:0;
    width: 100%;

}
.movieinfo {
    width: 80%;
    margin: 0 auto; 
    padding: 20px; 
    border: 1px dotted black; 
    height: 80vh; 
    overflow-y: auto;
    display: flex; 
    flex-direction: column;
    align-items: center;
}

form {
    display: flex;
    justify-content: center;
    align-items: center;
}

.search {
    background-color: #008140;
    padding: 20px 20px;
    height: 10px;
    color: white; 
    border-radius: 12px;
    display: block;
    line-height: 0px;
    font-size: 16px;
}

label {
    font-size: 20px;
}


#queryInput {
    box-shadow: 0 0 15px 4px rgba(0,0,0,0.06);
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
}
.movies {
    padding-top: 20px;
    
}

.movieContainer {
    border-style: ridge;
    border-width: 5px;
    border-radius: 10px;
    min-height: 420px;
    margin: 20px auto 45px; 
    box-shadow: 10px 5px 5px rgb(12, 12, 121);
    display: flex;
    justify-content: space-around;
    /* flex-direction: row; */
  }

  .movieImage {
    box-shadow: 10px 5px 5px black;
  }

  .movieBody {
      overflow-wrap: break-word;
      /* color: blue; */
  }
  
table {
    border-collapse: collapse;
}  

td, th {
    border: 1px solid #999;
    padding: 0.5rem;
    text-align: left;
}
  
  
